import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux'
import { reset_password_confirm } from '../actions/auth';

const ResetPasswordConfirm = ({ match, reset_password_confirm }) => {
    const [requestSent, setRequestSent] = useState(false)
    const [formData, setFormData] = useState({
        new_password: "",
        re_new_password: ""
    });

    const { new_password, re_new_password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault()
        if (new_password !== re_new_password) {
            alert("Tu contraseña debe coincidir")

        } else {
            const uid = match.params.uid;
            const token = match.params.token;
            reset_password_confirm(uid, token, new_password, re_new_password)
            alert("Se le envió un correo electrónico")
            setRequestSent(true)

        }

    }

    if (requestSent) {
        return <Redirect to='/' />
    }

    return (
        <>
            <div className='container mt-5'>

                <h1>Confirmación de restablecimiento de contraseña</h1>
                <form onSubmit={e => onSubmit(e)}>
                    <div className='form-group'>
                        <input
                            className='form-control'
                            type='password'
                            placeholder='Nueva contraseña *'
                            name='new_password'
                            value={new_password}
                            onChange={e => onChange(e)}
                            required

                        />
                    </div>
                    <div className='form-group'>
                        <input
                            className='form-control'
                            type='password'
                            placeholder='confirmar nueva contraseña *'
                            name='re_new_password'
                            value={re_new_password}
                            onChange={e => onChange(e)}
                            minLength='6'
                            required
                        />
                    </div>
                    <button className='btn btn-primary' type='submit'>Restablecer la contraseña</button>
                </form>

            </div>
        </>
    )
};

export default connect(null, { reset_password_confirm })(ResetPasswordConfirm);

